.loyer {
	display: flex;
	flex-direction: column;
	gap: 30px;
	align-items: center;
	width: 100%;
	min-height: 100vh;
	height: max-content;
	position: relative;
	background-color: #242424;
	color: #fff;
	&_chart {
		display: flex;
		flex-direction: column;
		gap: 10px;
		justify-content: flex-start;
		align-items: center;
		border-radius: 12px;
		border: 1px solid #70757a;
		background-color: #2e2e2e;
		color: #fff;
		width: 800px;
		height: 500px;
		padding: 10px;
		&_input_bloc {
			display: flex;
			flex-direction: column;
			justify-content: center;
			gap: 10px;
			align-items: flex-start;
			width: 100%;
			&_components {
				display: flex;
				width: 100%;
				justify-content: center;
				gap: 10px;
			}
		}
		&_input {
			border: 1px solid #70757a;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: flex-start;
			border-radius: 5px;
			padding: 5px 10px;
			& p {
				font-size: 18px;
			}
			&_components {
				display: flex;
				justify-content: center;
				align-items: center;
				& p {
					font-size: 18px;
					font-weight: bold;
				}
				& input {
					background-color: #2e2e2e;
					border: none;
					color: #fff;
					font-family: "Roboto", sans-serif;
					font-size: 24px;
					font-weight: bold;
					border-radius: 5px;
					padding: 5px 5px;
					width: 200px;
				}
			}
			& input {
				background-color: #2e2e2e;
				border: none;
				color: #fff;
				font-family: "Roboto", sans-serif;
				font-size: 24px;
				font-weight: bold;
				border-radius: 5px;
				padding: 5px 5px;
				width: 200px;
			}
		}
		& input {
			background-color: #2e2e2e;
			border: none;
			color: #fff;
			font-family: "Roboto", sans-serif;
			font-size: 24px;
			font-weight: bold;
			border-radius: 5px;
			padding: 5px 5px;
			width: 200px;
		}
	}
	&_graph_loyer_cumule {
		position: relative;
		width: 328px;
		height: 200px;
	}
	&_graph_interet {
		position: relative;
		width: 778px;
		height: 360px;
	}
	&_bloc_checkbox {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 10px;
	}
	&_checkbox {
		display: flex;
		align-items: center;
		height: 20px;
		border: 1px solid #fff;
		border-radius: 10px;
		width: 40px;
		padding: 0 2px;
		&_components {
			width: 16px;
			height: 16px;
			border: 1px solid #fff;
			border-radius: 50%;
			transition: transform 0.5s ease-in-out;
		}
		&_components.true {
			background-color: green;
			transform: translateX(18px);
		}
		&_components.false {
			background-color: red;
		}
	}
	&_expand {
		display: none;
		&_img {
			position: absolute;
			filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(143deg) brightness(103%) contrast(101%);
			cursor: pointer;
			right: 0;
			bottom: 0;
		}
		&_components {
			z-index: 1001;
			position: relative;
			display: flex;
			flex-direction: column;
			gap: 10px;
			justify-content: flex-start;
			align-items: center;
			border-radius: 12px;
			border: 1px solid #70757a;
			background-color: #2e2e2e;
			color: #fff;
			width: 1600px;
			height: 900px;
			padding: 10px 0;
			&_img {
				position: absolute;
				filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(143deg) brightness(103%) contrast(101%);
				cursor: pointer;
				right: 10px;
				top: 10px;
				z-index: 1100;
			}
			&_sub {
				width: 100%;
				height: 100%;
			}
		}
		&_chart {
			width: 100%;
			height: 780px;
		}
		&_title {
			width: 98%;
			display: flex;
			justify-content: space-between;
		}
		&_graph {
			z-index: 1001;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			border-radius: 12px;
			border: 1px solid #70757a;
			background-color: #2e2e2e;
			color: #fff;
			width: 800px;
			height: 900px;
			padding: 10px;
			&_sub {
				position: relative;
				display: flex;
				flex-wrap: wrap;
				justify-content: flex-start;
				align-items: center;
			}
			&_components {
				position: relative;
				height: 339px;
				width: 389px;
				& img {
					position: absolute;
					bottom: 10px;
					right: 10px;
					filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(143deg) brightness(103%) contrast(101%);
					cursor: pointer;
				}
			}
		}
		&_description {
			display: flex;
			gap: 100px;
			justify-content: center;
			align-items: center;
			width: 100%;
			&_sub {
				display: flex;
				flex-direction: column;
				gap: 10px;
			}
			&_paragraph_bloc {
				display: flex;
				gap: 10px;
			}
			&_paragraph {
				display: flex;
				flex-direction: column;
				gap: 10px;
			}
		}
	}
	&_expand.open {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		width: 100%;
		height: 100dvh;
		background-color: rgba(29, 29, 29, 0.5);
		z-index: 1000;
		top: 0;
	}
	&_title {
		display: flex;
		justify-content: space-between;
		width: 100%;
		padding: 0 35px;
	}
}
