  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: bold;
}
.dashboard_select {
  background-color: #2e2e2e;
  border: 1px solid #70757a;
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: bold;
  border-radius: 5px;
  padding: 5px 10px;
}
.dashboard_bloc_pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.dashboard_bloc_pagination_components {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  margin-right: 20px;
  position: relative;
}
.dashboard_bloc_pagination_components_page {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  background-color: #2e2e2e;
  border: 1px solid #70757a;
  padding: 5px 7px;
  border-radius: 5px;
  height: 40px;
  cursor: pointer;
}
.dashboard_bloc_pagination_components_page.selected {
  background-color: #3a3a3a;
}
.dashboard_bloc_pagination_components_img {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2e2e2e;
  border: 1px solid #70757a;
  padding: 5px 7px;
  border-radius: 5px;
  height: 40px;
  cursor: pointer;
}
.dashboard_bloc_pagination_components_img img {
  filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(143deg) brightness(103%) contrast(101%);
}
.dashboard_bloc_pagination_components_img.rotate {
  transform: rotate(180deg);
}
.dashboard_settings {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  border-radius: 12px;
  padding: 10px;
  background-color: #2e2e2e;
}
.dashboard_settings_key {
  display: none;
}
.dashboard_settings_key.open {
  display: flex;
  position: absolute;
  top: 75px;
  right: 10px;
  z-index: 900;
}
.dashboard_expand {
  width: 1518px;
  height: 900px;
}
.dashboard_expand_box {
  width: 1518px;
  height: 818px;
